import { PLATFORM_PAYMENT_METHODS } from "../../utils/Constants";

const PaymentMethods = () => {
  return (
    <div className="PAYMENT-METHODS flex flex-wrap justify-center items-center gap-x-8 gap-y-2">
      <h4 className="text-sm font-semibold uppercase">100% Secure Payment</h4>
      <div className="flex items-center">
        {PLATFORM_PAYMENT_METHODS.map((method, index) => (
          <img
            key={index}
            src={method}
            alt={`payment-method-${index}`}
            className="h-6 w-12 object-contain"
          />
        ))}
      </div>
    </div>
  );
};

export default PaymentMethods;
