import gplayLogo from "../../assets/images/Google Play.png";
import appleLogo from "../../assets/images/Apple Store.png";
import whatsapp from "../../assets/icons/whatsapp.svg";
import { FiSmartphone } from "react-icons/fi";
import { BsFillTelephoneFill } from "react-icons/bs";
import NewsLetter from "../../components/NewsLetter";
import useMediaQuery from "../../hooks/useMediaQuery";

const Contacts = () => {
  const maxLg = useMediaQuery("(min-width: 1024px)");

  return (
    <div className="CONTACTS-SECTION flex max-md:flex-col justify-between gap-y-4">
      <div className="CTA flex flex-col items-center gap-y-2">
        <div className="flex items-center gap-2 text-lg text-white">
          <FiSmartphone />
          <h4 className="font-semibold">DOWNLOAD OUR APP</h4>
        </div>
        <div className="flex gap-2">
          <img src={gplayLogo} alt="Google Play" className="h-8 w-24" />
          <img src={appleLogo} alt="App Store" className="h-8 w-24" />
        </div>
      </div>

      <div className="CONTACT-INFO text-sm text-white max-md:w-5/6 max-md:mx-auto max-md:text-center">
        <p className="text-lg max-md:text-base">
          For any queries, please call us
        </p>
        <div className="flex justify-center gap-4">
          <div className="flex items-center max-md:justify-center gap-2">
            <BsFillTelephoneFill />
            <a
              href="tel:+917595878899"
              target="_blank"
              rel="noopener noreferrer"
              className="font-semibold"
            >
              7595878899
            </a>
          </div>
          <div className="flex items-center max-md:justify-center gap-2">
            <img src={whatsapp} alt="whatsapp" />
            <a
              href="https://wa.me/7595878899"
              target="_blank"
              rel="noopener noreferrer"
              className="font-semibold"
            >
              Whatsapp
            </a>
          </div>
        </div>
      </div>

      {maxLg && <NewsLetter />}
    </div>
  );
};

export default Contacts;
