import { PLATFORM_SOCIALS } from "../../utils/Constants";

const Socials = () => {
  return (
    <div className="SOCIALS-SECTION flex flex-col items-center gap-y-1 max-lg:pt-4">
      <h4 className="text-xs font-medium">Show us some love on social media</h4>
      <div className="flex gap-2 mb-4">
        {PLATFORM_SOCIALS.map((profile, index) => (
          <a
            key={index}
            href={profile.url}
            target="_blank"
            rel="noreferrer"
            className="p-1 text-white rounded-full"
            style={{ backgroundColor: profile.accent }}
          >
            {profile.icon}
          </a>
        ))}
      </div>
    </div>
  );
};

export default Socials;
