import { PLATFORM_FEATURES } from "../../utils/Constants";

const Tab = ({ icon, title, description }) => {
  return (
    <div className="w-50 flex max-lg:flex-col max-lg:items-center lg:gap-2">
      <div className="ICON size-12 max-lg:size-10 flex items-center justify-center text-3xl max-lg:text-2xl text-primeRed border-2 border-primeRed rounded-full flex-shrink-0">
        {icon}
      </div>
      <div className="BODY lg:space-y-2 pt-2 max-lg:text-center">
        <h3 className="uppercase text-sm font-medium">{title}</h3>
        <p className="text-xs text-primary">{description}</p>
      </div>
    </div>
  );
};

const Feature = () => {
  return (
    <div className="flex justify-center gap-4 max-lg:pb-4">
      {PLATFORM_FEATURES.map((feature) => (
        <Tab
          key={feature.title}
          icon={feature.icon}
          title={feature.title}
          description={feature.description}
        />
      ))}
    </div>
  );
};

export default Feature;
