import NewsLetter from "../../components/NewsLetter";
import useMediaQuery from "../../hooks/useMediaQuery";
import Contacts from "./Contacts";
import Feature from "./Feature";
import Legal from "./Legal";
import PaymentMethods from "./PaymentMethods";
import QuickLinks from "./QuickLinks";
import Socials from "./Socials";

const Footer = () => {
  const maxLg = useMediaQuery("(min-width: 1024px)");

  return (
    <footer>
      <nav>
        <section className="TOP container max-w-full bg-darkGray md:py-6 py-4">
          <Contacts />
        </section>
        <section className="MIDDLE-1 container max-w-full bg-blueGray md:py-6 py-4">
          <QuickLinks />
        </section>
        <section className="MIDDLE-2 container max-w-full bg-white md:py-6 py-4">
          <div className="flex max-lg:flex-col justify-around gap-y-4 border-b border-primary md:pb-6 pb-4 max-lg:divide-y max-lg:divide-primary">
            <Feature />
            <Socials />
          </div>
          <div className="md:pt-6 pt-4">
            <PaymentMethods />
          </div>
        </section>
        {!maxLg && (
          <section className="OPTIONAL container max-w-full bg-darkBlue md:py-6 py-4">
            <NewsLetter />
          </section>
        )}
        <Legal />
      </nav>
    </footer>
  );
};

export default Footer;
