import { TbMailDown } from "react-icons/tb";

const NewsLetter = () => {
  return (
    <div className="NEWSLETTER">
      <form className="space-y-1">
        <label
          htmlFor="newsletter"
          className="flex items-center gap-2 text-sm text-white font-medium"
        >
          <TbMailDown />
          <p>Get latest updates on your inbox</p>
        </label>

        <div className="flex justify-between gap-2 text-sm text-white">
          <input
            type="email"
            placeholder="Your Email"
            className="p-2 w-full bg-transparent border-b-3 outline-none border-white placeholder:text-white"
          />
          <button className="border-2 border-white font-medium px-4 py-2">
            SEND
          </button>
        </div>
      </form>
    </div>
  );
};

export default NewsLetter;
