import Logo from "../../components/Logo";
import { footerLinks } from "../../utils/Constants";

const QuickLinks = () => {
  return (
    <div className="QUICK-LINKS flex justify-around max-md:flex-col gap-x-32 gap-y-4">
      <div className="text-sm flex-shrink-0">
        <div className="space-y-2 max-md:flex max-md:flex-col max-md:items-center">
          <a href="/" className="LOGO inline-block w-32 max-md:w-28">
            <Logo color="white" />
          </a>

          <ul className="w-full space-y-1 max-md:flex max-md:justify-center max-md:items-center max-md:gap-x-8 max-sm:gap-x-4">
            {footerLinks["main"].map((link) => (
              <li key={link.title}>
                <a
                  href={link.url}
                  className="capitalize max-md:text-xs text-white hover:text-lavender transition-colors duration-150 ease-in-out"
                >
                  {link.title}
                </a>
              </li>
            ))}
          </ul>
        </div>
      </div>

      <div className="max-md:w-full grid grid-cols-3 gap-x-16 max-lg:gap-x-8 max-md:gap-x-4 max-sm:gap-x-2 text-sm">
        {Object.keys(footerLinks)
          .slice(1, Object.keys(footerLinks).length)
          .map((key) => (
            <div key={key} className="space-y-2 mx-auto">
              <h4 className="text-lg max-md:text-sm font-semibold text-warning uppercase">
                {key}
              </h4>

              <ul className="md:space-y-1">
                {footerLinks[key].map((link) => (
                  <li key={link.title}>
                    <a
                      href={link.url}
                      className="capitalize max-md:text-xs text-white hover:text-lavender transition-colors duration-150 ease-in-out"
                    >
                      {link.title}
                    </a>
                  </li>
                ))}
              </ul>
            </div>
          ))}
      </div>
    </div>
  );
};

export default QuickLinks;
